body {
  font-family: timenewromans;
    color: white;
    background:#a5e1dd;
}

.sections{
	position: relative;
    top: 0;
    left: 0;
	background-color:white;
	opacity: .6;
    color: #133937;
}
h1, h2, p, ul, li {
  font-family: sans-serif;
}

ul.header li {
  display: inline;
  list-style-type: none;
  margin: 0;
}

ul.header {
  background-color: #111;
  padding: 0;
}
ul.header li a {
  color: #FFF;
  font-weight: bold;
  text-decoration: none;
  padding: 20px;
  display: inline-block;
}
.content {
  background-color: #FFF;
  padding: 20px;
}
.content h2 {
  padding: 0;
  margin: 0;
}
.content li {
  margin-bottom: 10px;
}
span{
color:grey;
}
p{
font-size:17pt;
}


